import React from "react";

function LogoTexto() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="336"
      height="418"
      viewBox="0 0 336 418"
    >
      <image
        id="Logo-Brinde-na-Europa-Red"
        width="336"
        height="418"
        xlinkHref="data:img/png;base64,"
      ></image>
    </svg>
  );
}

export default LogoTexto;