import React from "react";

function Logo() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="236"
      height="245"
      viewBox="0 0 236 245"
    >
      <image
        id="logo-campanha-red"
        width="236"
        height="245"
        xlinkHref="data:img/png;base64,"
      ></image>
    </svg>
  );
}

export default Logo;